import React from 'react'
import { useHistory ,Link, NavLink   } from "react-router-dom"
import { Form, Input, Cascader, Select, Row, Col, Checkbox, Button, AutoComplete, Card } from 'antd';
import axios from 'axios';
import { BASE_URL } from '../../constants/index';
import Swal from "sweetalert2";
import LocaleProvider from 'antd/lib/locale-provider';

const formItemLayout = {
    labelCol: {
        xs: {
            span: 24,
        },
        sm: {
            span: 8,
        },
    },
    wrapperCol: {
        xs: {
            span: 24,
        },
        sm: {
            span: 16,
        },
    },
};
const tailFormItemLayout = {
    wrapperCol: {
        xs: {
            span: 24,
            offset: 0,
        },
        sm: {
            span: 16,
            offset: 8,
        },
    },
};

const Login = () => {
    let history = useHistory()

    const [form] = Form.useForm();
    const onFinish = async (values) => {
        console.log('Received values of form: ', values);




        // console.log(body);
        try {
            const config = { headers: { "Content-Type": "multipart/form-data" } };

            await axios.post(`${BASE_URL}/pointOfSellLogin`, {
                ...values,
            }).then((response) => {
                console.log(response.data.result._id);
                localStorage.setItem("token", response.data.token)
                localStorage.setItem("sell_point_id", response.data.result._id)
            })

                ;
            Swal.fire("رائع!", "لقد قمت بتسجيل الدخول  بنجاح!", "success").then(
                (result) => {
                    history.push("/")

                    if (result.isConfirmed || result.isDismissed) {
                        console.log("done");
                    }
                }
            );
        } catch (error) {
            if (error.response) {

                Swal.fire({
                    icon: "error",
                    title: "Oops...",
                    text: error.response.data.msg,
                });
                console.log("error", error.response.data);
            }
        }



    };
    return (
        <Row>
            <Card style={{ width: '100%' }}>

            
            <Col span={24} justify='space-between' align='middle' style={{ opacity: 0.5, color: 'black', margin:'50px' }}>
            <div className="w-75 mx-auto shadow p-5">

                <Form
                    {...formItemLayout}
                    form={form}
                    name="register"
                    onFinish={onFinish}
                    initialValues={{
                        residence: ['zhejiang', 'hangzhou', 'xihu'],
                        prefix: '86',
                    }}
                    scrollToFirstError
                    style={{ width: '50%', marginTop: '200px' }}
                >
                    <Form.Item
                        name="email"
                        label="البريد الالكتروني"
                        rules={[
                            {
                                type: 'email',
                                message: 'الرجاء إدخال بريد الكتروني صحيح ',
                            },
                            {
                                required: true,
                                message: 'الرجاء ادخال البريد الالكتروني !',
                            },
                        ]}
                    >
                        <Input style={{ margin: 15 }} />
                    </Form.Item>

                    <Form.Item
                        name="password"
                        label="كلمة المرور "
                        rules={[
                            {
                                required: true,
                                message: 'الرجاء إدخال كلمة المرور !',
                            },
                        ]}
                        hasFeedback
                    >
                        <Input.Password style={{ margin: 15 }} />
                    </Form.Item>





                    <Form.Item {...tailFormItemLayout}>
                        <Button type="primary" htmlType="submit" style={{ margin: 15 }}>
                            تسجيل دخول
                        </Button>
                        <Link to ="/register">
                        ليس لديك حساب تسجيل
                        </Link>
                    </Form.Item>
                </Form>
                </div>
            </Col>
            </Card>
        </Row>
    )
}

export default Login
